
import { Award, Gem, Star, Trophy, Heart, PlusCircle } from "lucide-react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Progress } from "@/components/ui/progress";
import { Button } from "../ui/button";
import { getLevelProgress, xpForLevel } from "@/lib/xp";
import { cn } from "@/lib/utils";

export const badges = [
  { icon: Star, color: "text-yellow-400", label: "First Steps" },
  { icon: Award, color: "text-blue-400", label: "Math Whiz" },
  { icon: Trophy, color: "text-green-400", label: "Reading Champ" },
  { icon: Gem, color: "text-purple-400", label: "Creative Genius" },
];

const HEAL_COST = 5;
const HEAL_AMOUNT = 20;

type ProgressTrackerProps = {
  hp: number;
  setHp: React.Dispatch<React.SetStateAction<number>>;
  xp: number;
  level: number;
  treasures: number;
  setTreasures: React.Dispatch<React.SetStateAction<number>>;
  earnedBadges: string[];
}

export default function ProgressTracker({ hp, setHp, xp, level, treasures, setTreasures, earnedBadges }: ProgressTrackerProps) {
  const handleHeal = () => {
    if (treasures >= HEAL_COST && hp < 100) {
      setHp(currentHp => Math.min(100, currentHp + HEAL_AMOUNT));
      setTreasures(currentTreasures => currentTreasures - HEAL_COST);
    }
  }
  
  const progress = getLevelProgress(xp);
  const xpForNextLevel = xpForLevel(level + 1) - xp;
  const canHeal = treasures >= HEAL_COST && hp < 100;

  return (
    <Card className="shadow-lg hover:shadow-xl transition-shadow duration-300">
      <CardHeader>
        <CardTitle className="flex items-center gap-2 font-headline">
          <Trophy className="text-accent" />
          My Progress
        </CardTitle>
      </CardHeader>
      <CardContent className="space-y-6">
        <div className="text-center">
          <p className="text-sm text-muted-foreground">Current Level</p>
          <p className="text-5xl font-bold text-primary">{level}</p>
        </div>
        <div className="space-y-2">
            <div className="flex justify-between text-sm font-medium">
                <span className="text-muted-foreground">Level Progress</span>
                <span className="text-primary-foreground">{progress}%</span>
            </div>
          <Progress value={progress} className="h-3" />
          <p className="text-xs text-muted-foreground text-center pt-1">{xpForNextLevel} XP to next level!</p>
        </div>

        <div className="space-y-2">
          <div className="flex justify-between items-center">
            <h4 className="font-semibold text-foreground flex items-center gap-2"><Heart className="text-red-500"/> Health</h4>
            <span className="font-bold text-lg">{hp}/100</span>
          </div>
          <Progress value={hp} className="h-4 [&>div]:bg-red-500" />
          <div className="flex justify-center">
             <Button onClick={handleHeal} disabled={!canHeal} size="sm" variant="outline" className="mt-2">
              <PlusCircle className="mr-2 h-4 w-4"/>
              Heal ({HEAL_AMOUNT} HP) - {HEAL_COST}
              <Gem className="ml-2 h-4 w-4 text-accent"/>
            </Button>
          </div>
        </div>

        <div className="space-y-3">
          <h4 className="font-semibold text-foreground">Hero Badges</h4>
          <div className="grid grid-cols-4 gap-4">
            {badges.map((badge) => {
              const isEarned = earnedBadges.includes(badge.label);
              return (
                <div key={badge.label} className={cn("flex flex-col items-center text-center gap-1 transition-all", isEarned ? "opacity-100" : "opacity-40 grayscale")} title={badge.label}>
                  <div className={cn("p-3 bg-muted rounded-full relative transition-all", isEarned && "bg-accent/20")}>
                    <badge.icon className={cn("w-6 h-6", badge.color, isEarned && "drop-shadow-[0_0_4px_currentColor]")} />
                  </div>
                  <p className="text-xs text-muted-foreground truncate">{badge.label}</p>
                </div>
              )
            })}
          </div>
        </div>
        <div className="flex items-center justify-between bg-muted/50 p-3 rounded-lg">
          <h4 className="font-semibold text-foreground">Treasures</h4>
          <div className="flex items-center gap-2 font-bold text-accent">
            <Gem />
            <span>{treasures}</span>
          </div>
        </div>
      </CardContent>
    </Card>
  );
}
